import React, { Component } from 'react';
import { GET_FIRST_LETTER, GET_NETWORK_COLOR } from '../../../../../utils';

class ProfileImage extends Component {
    constructor(props) {
        super(props)
        this.state = {
            profileError: false
        }
    }


    render() {

        const { profileError } = this.state;
        const { imageSize, symbolClass, className, name, profileUrl, networkId } = this.props;
        return <div className={className}>
            {profileError || (profileUrl == "") ?
                <span className={symbolClass} style={{ backgroundColor: networkId?GET_NETWORK_COLOR(networkId):null }}>{GET_FIRST_LETTER(name)}</span>
                :
                <img src={profileUrl} height={imageSize} width={imageSize} alt={name} onError={() => this.setState({ profileError: true })} />
            }
        </div>
    }
}

export default ProfileImage;